#footer{
    width: 100%;
    height: 150px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background-color: #4B49B7;
    color: white;
    font-size:12px;
}
.textCenter{
    text-align: center;
}
.footerContainer-1{
    font-size: small;
    margin-top: 18px;
}
.footerContainer-1 > div{
    margin: 9px;
}
.footerContainer-1 > div > a{
    color: white;
    text-decoration: none;
}
.footerContainer-2{
    font-size: 1em;
    height: 50px;
    width: 100%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #615fd1;
    
}

@media only screen and(max-width:500px){
    .footerContainer-1{
        font-size: x-small;
    }
}