.noti-main-container{
    max-width: 800px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.noti-box{
    width: 100%;
    border: 1px solid #e83b8c;
    border-radius: 4px;
}

.noti-box-before::before{
    content: "";
    width: 10px;
    height: 10px;
    z-index: -1;
    border-radius: 24px;
    float: right;
    background: #e83b8c;
}

.noti-outer{
    display: flex;
    flex-wrap: wrap;
}
.noti-title-head,.noti-desc-head,.noti-replay-head{
    width: 90px;
}