*{
  margin:0;
  padding:0;
}

code{
  padding: 4px;
}

.pointer{
  cursor: pointer;
}

.active{
  color: #F48144 !important;
}

.input-all-unset{
  all: unset;
}
.input-border-bottom{
  border-bottom: 2px solid #ff7b65;
}

hr {
  margin: 1rem 0;
  color: inherit;
  background-color: #E83B8C;
  border: 0;
  opacity: 1;
}

a{
  color:#E83B8C;
  width: 100%;
  height: 100%;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}