.acc-form {
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 4px 70px;
}
.main-container{
    display: flex;
    justify-content: center;
    align-items: center;
}
.acc-ill {
    width: 100%;
}
.acc-submit{
    background-color: #ff7b65;
    color: white;
}
.acc-submit:hover{
    border: 1px solid red;
    color: white;
}
.acc-active-page-line-1 , .acc-active-page-line-2{
    height: 4px !important;
    opacity: 1;
    margin: 4px 0px;
    transition: all 300ms linear;
}
.acc-active-page-line-1{
    float: right;
}

.acc-login-head,.acc-sign-up-head{
    color: black;
    transition: border 400ms;
    padding: 0px 8px;
}
#massage{
    width: 100%;
    position: absolute;
    margin-top: 45px;
    display: flex;
    justify-content:flex-end;
}
@media only screen and (max-device-width:780px){
    .main-container{
        flex-direction: column-reverse;
    }
    .acc-form{
        width: 80%;
    }
    .acc-ill{
        width: 70%;
    }
}


.acc-signup {
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 4px 70px;
}
.fadeInUpEffect-1{
    animation: fadeInUpAnime 2s 1s  forwards !important;
}
.fadeInUpEffect-2{
    animation: fadeInUpAnime 2s 1s  forwards !important;
}
@keyframes fadeInUpAnime {
    0%{
        margin-top: 300px;
        opacity: 0;
    }
    100%{
        margin-top: 0px;
        opacity: 1;
    }
}
@media only screen and (max-device-width:780px) {

    .acc-signup {
        width: 80%;
    }

}

@media only screen and (max-device-width: 550px) {

    .acc-form{
        width: 100%;
    }
    .acc-signup{
        width: 100%;
    }
    
}