#navBar{
    background-color: #4B49B7;
    height:70px;
    display:flex;
    justify-content: space-between;
}

#brandName{
    padding-left: 10px;
    white-space: nowrap;
}
#brandImage > img{
    height: 40px;
    padding-left: 30px;
}
#navBarItems{
    display: flex;
    margin: 20px 25px;
}
#navBarItems > li{
    display: flex;
    padding: 6px 16px;
    color: white;
    font-size: large;
}

#navBarItems > li a:hover{
    color: #d5713b !important;
}
#hamBurger{
    margin: 17px;
    background-color: #615fd1;
    display: none;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 9px;
    border:2px solid rgb(255, 255, 255);
    padding:4px;
}

#hamBurgerMeanu{
    padding:0px;
    color: white;
    margin: 0px;
    flex-direction: column;
    background-color: #615fd1;
    transition: all 400ms;
}

#hamBurgerMeanu > li{
    display: flex;
    padding: 7px 14px;
    transition: all 400ms;
}

#hamBurgerMeanu > li a:hover{
    color: hsl(40, 6%, 80%)!important;
}

.stripes{
    opacity: 1;
    margin:3px;
    width: 20px;
    border: 1px solid rgb(255, 255, 255);
    background-color: white;
    transition: all 400ms;
}

.navLinks > a{
    color: white;
    text-decoration: none;
}

.auth-btn{
    width: 70px;
    background-color: #4B49B7;
    color: darkblue;
    border: 1px solid rgb(255, 255, 255);
    border-radius: 4px;
    margin: 0px 3px;
    cursor: pointer;
}
.auth-btn:hover{
    background-color: #3330cc;
}
.pointer{
    cursor: pointer;
}

.navSearch::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: rgb(255, 255, 255);
    opacity: 1; /* Firefox */
}
  
.navSearch:-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: rgb(255, 255, 255);
}
  
.navSearch::-ms-input-placeholder { /* Microsoft Edge */
    color: rgb(255, 255, 255);
}

.navSearch{
    color: white !important;
    border-bottom: 1px solid white !important;
}
@keyframes animeOfOpenMeanu{
    0%{
        height: 0px;
    }
    100%{
        height: 200px;
    }
}
@keyframes animeOfCloseMeanu{
    0%{
        height: 200px;
    }
    100%{
        height: 0px;
    }
}

@media only screen and (max-width: 750px){
    #navBarItems{
        display: none;
    }
    #brandImage > img{
        height: 40px;
        padding-left: 20px;
    }
    #hamBurger{
        display:flex;
    }
}
@media only screen and (min-width:750px){
    #hamBurgerMeanu{
        display:none !important;
    }
}

@media only screen and (max-width:880px){
    #navBarItems > li {
        padding: 6px 13px;
        font-size: medium;
    }
    #brandImage > img {
        height: 32px;
        padding-left: 9px;
    }
}

