.articlesContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 3px;
    flex-wrap: wrap;
    padding: 14px;
}
.mainPageArticle{
    display: flex;
    border-radius: 2px;
    margin: 6px 4px;
    padding: 7px;
    height: 134px;
    width: 500px;
    border: 1px solid rgb(0,0,0,0.25);
    font-size: medium;

}

.mainPageArticleContent-1{
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin: 4px;
    padding:4px;
}
.mainPageArticleContent-3{
    margin: 6px 0px;
    display: flex;
    flex-direction: column;
    font-size: x-small;
}
.mainPageArticleImage{
    height: 100%;
    width: 135px;
}
.infinite-scroll-component{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 1300px;
    overflow: visible !important;
}
.show-more-items{
    border-bottom: 2px solid rgb(0,0,0,0.25);
    height: 50px;
    transform: translateY(-40px);
    background: radial-gradient(#a7a1a18c, #ffffff00);
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}
.show-more-items-outer{
    width: 100%;
    margin: 2px auto;
}
@media only screen and (max-width:1090px){
    .mainPageArticle{
        font-size: smaller;
        width: 400px;
    }
}
@media only screen and (max-width:886px){
    .mainPageArticle{
        width: 300px;

    }
}
@media only screen and (max-width:786px){
    .mainPageArticle{
        width: 100%;
        font-size: medium;

    }
}
@media only screen and (max-width:500px){
    .mainPageArticle{
        font-size: small;
    }
}
@media only screen and (max-width:385px){
    .mainPageArticle{
        font-size: small;
        height: 120px;
    }
    .mainPageArticleImage{
        width:100px;
    }

}
@media only screen and (max-width:300px){
    .mainPageArticle{
        height: 110px;
    }
    .mainPageArticleImage{
        width:90px;
    }
    .mainPageArticleContent-3{
        font-size: xx-small;
    }
}


