.handles-outer{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.handles-image{
    height: 100px;
}
.handles-image-outer{
    width: fit-content;
    border-radius: 13px;
    padding: 8px;
    border: 2px solid black;
    transition: all 300ms;
}
.handles-link-outer{
    opacity: 0;
    transition: all 400ms;
}
.handles-outer:hover > .handles-image-outer{
    filter:blur(4px);
    
}
.handles-outer:hover > .handles-link-outer{
    opacity: 1;
    transform: translateY(-85px);
}
.handles-link a{
    text-decoration: none;
    color: rgb(0, 0, 0)
}