#mainIl{
    height: 47vw;
    width: 100%;   
}
#mainIlParent{
    width: 50%;
}
#landPage{
    display: flex;
}
#mainGreeting{
    white-space: nowrap;
    padding: 4px 14px;
    box-sizing: border-box;
    width: 50vw;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    font-size: 1.3vw;
}
#mainPara{
    margin-bottom: 9px;
    font-size: 1em;
}
#mainExploreBtn {
    padding: 6px;
    margin: 8px;
    border-radius: 3px;
    background-color: #ff7b65;
    border: 1px solid #ff7b65;
    color: white;
}
#mainExploreBtn > a{
    color: white;
    text-decoration: none;
}
p{
    margin: 0px;
}
h1{
    margin: 0px;
}
h2{
    margin:0px;
}
h4{
    margin:0px;
}
.mainH1{
    font-size: 1.9em;
    margin: 0px;
}
.mainH4{
    margin:12px 3px;
    font-size: 1.3em;
}



