@media only screen and (max-width:700px) {
    #landPage{
        flex-direction: column-reverse;
    }
    #mainGreeting{
        font-size: 2.7vw;
        width: 100%;
    }
    #mainIl{
        width: 60%;
    }
    #mainIlParent{
        width: 100vw;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}