body{
    min-width: 300px;
}
.acc-home{
    height:100vh;
}
.acc-main{
    margin-top: 60px;
}
.acc-container-1{
    display: flex;
    justify-content: center;
    align-content: center;
    margin:auto;
}
.acc-container-1 h4{
    padding:16px;
    border-bottom:1px solid black;
}
.acc-container-2{
    width: 90%;
    margin: 16px auto;
}
.acc-container-2 div i{
    margin-left:12px;
}
.cursor-pointer{
    cursor: pointer;
}

.btn-logout{
    width: fit-content;
    background-color: #ff7b65;
    border: 1px solid #f2664d;
    border-radius:4px;
}
.btn-logout button,.btn-logout button:hover {
    color: white;
}