.comments-main{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.comments-form-outer{
    width: 100%;
    display: flex;
}
.comments-input{
    width: 100% !important;
    all: unset;
    border-bottom: 2px solid #ff7b65;
}
.comments-input-btn{
    width: 60px;
    margin-left: 4px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 0px;
    padding: 4px;
    border: 2px solid #ff7b65;
    background-color: #ff7b65;
    color: white;
}
.comment-outer{
    /* border: 1px solid rgb(0,0,0,0.25); */
    border-bottom:1px solid #e83b8c70;
    border-left: 3px double #E83B8C;
    border-bottom-left-radius: 4px;
    padding: 8px;
    display: flex;

}
.comment{
    display: flex;
    flex-direction: column;

}
.comment-text-outer{
    margin-left: 12px;
}
.comment-image-outer{
    margin: auto 4px;
    width: 50px;
}
.comment-image{
    width: 50px;
    border: 2px solid black;
    border-radius: 50%;
}
.comment-replay-date{
    width: 100%;
    font-size: medium;
    margin-left: 30px;
    display: flex;
}
.comment-replay{
    margin-right: 30px;
    cursor: pointer;
}
.replay-forms{
    display:none;
}
.comment-break-hr{
    display: none;
    margin: 0px;
    transform: translateX(-9px);
}

@media only screen and (max-width:400px){
    .comment-replay-date{
        font-size: smaller;
        width: 100%;
        justify-content: space-evenly ;
        margin: 6px 0px 0px 0px;
    }
    .comment-outer{
        display: flex;
        flex-direction: column;
    }
    .comment-break-hr{
        display: block;
    }
}